import { dbg as clog, time } from './utils'
import _ from 'lodash'
import { verbkeys } from './verbkeys'
const d = require('debug')('app')
import {oxia, comb, plain} from 'orthos'

let log = console.log

// verbs строятся по разным stems, т.е. один verb может порождать один chain по разным stems :
export function parseVerb (verbs, flexes) {

  let only = 'βαρυν'
  let vdicts = []

  verbs.forEach(dict => {
    // if (dict.plain == only) d('NC-d ===========================>>>', dict)
    // βραχυνομένῳ - это ужас, но работает. Нужно привести в порядок

    if (dict.reg && dict.key && !dict.vkeys) {
      let vk = verbkeys[dict.key]
      if (!vk) return false
      dict.vkeys = verbkeys[dict.key].vkeys, dict.pkeys = verbkeys[dict.key].pkeys, dict.ikeys = verbkeys[dict.key].ikeys
    }

    if (dict.reg && dict.vkeys && (!dict.pkeys || !dict.ikeys) ) {
      let vkey = dict.vkeys.pres[0]
      dict.pkeys = verbkeys[vkey].pkeys
      dict.ikeys = verbkeys[vkey].ikeys
    }
    // else
    // пока что силовое решение, нужно проверить - κλίνεσθαι дает ошибку с else, а зачем тут else?
    if (dict.reg && (!dict.vkeys || !dict.pkeys || !dict.ikeys) ) return false // LJS unknowns - μακρῶν - error - Cannot read property 'pres' of undefined

    let fls = _.map(flexes, flex => {
      // if (dict.plain == only && flex.tense == 'act.pres.ind') d('NC-f ------------------------', flex)

      if (dict.reg != flex.reg) return false
      // if (dict.sliced && flex.part) return false // причастия не имеют augs ἄγω - ἦγον; неверно же: βδέω - ἐβδεύσας
      if (dict.reg) {
        // if (dict.plain == only) log('NC-d __________________________________________________', dict.pkeys, 'f', flex.pkey)
        if (flex.vkey && !dict.vkeys[flex.time]) return false
        else if (flex.vkey && !dict.vkeys[flex.time].includes(flex.vkey)) return false
        if (flex.pkey && !dict.pkeys[flex.time]) return false
        else if (flex.pkey && !dict.pkeys[flex.time].includes(flex.pkey)) return false
        if (flex.ikey && !dict.ikeys[flex.time]) return false
        else if (flex.ikey && !dict.ikeys[flex.time].includes(flex.ikey)) return false
      } else {
        if (dict.time != flex.time) return false
        if (flex.vkey && dict.vkey != flex.vkey) return false
        if (flex.pkey && dict.pkey != flex.pkey) return false
        if (flex.ikey && dict.ikey != flex.ikey) return false
      }
      let cflex
      if (flex.numcase) cflex = {tense: flex.tense, numcase: flex.numcase, gend: flex.gend}
      else cflex = {tense: flex.tense, numper: flex.numper}
      // return true
      return cflex
    })
    fls = _.compact(fls)

    if (!fls.length) return
    // if (dict.plain == only) d('NC-FLS ===========================>>>', dict.rdict, fls.length)

    if (fls.length) {
      let cleandict = {verb: true, time: dict.time, rdict: dict.rdict, plain: dict.plain, dname: dict.dname, trns: dict.trns, weight: dict.weight }
      let dictkey = [dict.rdict, dict.dname].join('-')
      let res = {dictkey: dictkey, dict: cleandict, fls: fls}
      vdicts.push(res)
    }
  })

  let cdicts = []
  let groupdict = _.groupBy(vdicts, 'dictkey')
  for (let dictkey in groupdict) {
    let cdict = groupdict[dictkey][0].dict
    let fls = groupdict[dictkey].map(group=> { return group.fls })
    let cfls = []
    let keyfls = {}
    _.flatten(fls).forEach(flex=> {
      let keyflex = JSON.stringify(flex)
      if (keyfls[keyflex]) return
      cfls.push(flex)
      keyfls[keyflex] = true
    })
    cdict.fls = cfls
    if (cdict.plain == only) d('CDICT-FLS ===========================>>>', cdict, cfls.length)
    cdicts.push(cdict)
  }

  return cdicts
}

// NAME
export function parseName (names, flexes) {
  let nchains = []
  // let last = _.last(segs)
  let nnames = _.filter(names, dict => { return dict.name })
  let nflexes = _.filter(flexes, flex => { return flex.name })
  // let advflexes = _.filter(flexes, flex => { return flex.adv })

  let only = 'υπαρξ_'

  names.forEach(dict => {
    if (dict.added || dict.sliced) return false
    if (dict.plain == only) d('NAME-d ===========================>>>', dict)

    let fls = _.filter(flexes, flex => {
      // if (dict.plain == only) log('NAME-f =======>', flex)
      if (dict.plain == only && flex.key == 'ις-εως') d('NAME-f =======>', flex)

      if (!dict.keys) return false
      if (dict.gends) {
        // if (flex.ends) return false
        // убрал из-за неточного определения keys и gends / terms в LSJ и DVR, чтобы окончания прилагательных проходили - см. συλλαβή, но теперь двойные σύμφωνα, нужно uniq
        if (!dict.gends.includes(flex.gend)) return false
      } else if (flex.adv) { // не помно, нужен пример
        if (!dict.keys.includes(flex.key)) return false
      } else {
        if (!flex.ends) return false
        if (dict.ends != flex.ends) return false
      }

      if (dict.dname == 'lsj' && dict.ends) {
        if (dict.plain == only) log('ADKEY________________', dict.keys, flex.adkey)
        if (!dict.keys.includes(flex.adkey)) return false
      } else {
        if (!dict.keys.includes(flex.key)) return false
      }

      return true
    })

    if (dict.plain == only) d('NAME-flex.size ===========================>>>', fls.length)
    if (!fls.length) return


    let cleanfls = fls.map(flex => { return {numcase: flex.numcase, gend: flex.gend} })
    dict.fls = compactNameFls(fls)
    nchains.push(dict)
  })
  return nchains
}

function compactNameFls(fls) {
  let cleanfls = []
  let flkey = {}
  fls.forEach(flex=> {
    let clflex
    if (flex.adv) clflex = { adv: true, term: flex.term, degree: flex.degree }
    else clflex = { name: true, ends: flex.ends, term: flex.term, numcase: flex.numcase, gend: flex.gend }
    // let flexkey = JSON.stringify(clflex)
    let flexkey = [clflex.gend, clflex.numcase].join('-')
    if (flkey[flexkey]) return
    cleanfls.push(clflex)
    flkey[flexkey] = true
  })
  return cleanfls
}

function uniqDict(dicts) {
  let udicts = []
  let uvkeys = {}
  dicts.forEach(dict => {
    if (!dict.trns) dict.trns = 'no trns: ' + dict.rdict
    let uvkey = [dict.rdict, dict.dbname, dict.trns.toString()].join('')
    if (uvkeys[uvkey]) return
    let udict = {verb: true, time: dict.time, rdict: dict.rdict, plain: dict.plain, dname: dict.dname, trns: dict.trns, weight: dict.weight, fls: dict.fls}
    // let udict = dict
    if (dict.reg) udict.reg = true
    udicts.push(udict)
    uvkeys[uvkey] = true
  })
  return udicts
}

