export function antrax (wf, compound) {
  let cwf = corrStr(wf)
  cwf = cwf.replace(/-/g, '')
  cwf = enclitic(cwf)
  // compound = true

  let sgms = segmenter(cwf)
  if (compound) sgms = _.filter(sgms, sgm=> { return sgm.length > 2 })
  // lasts - segments for flexes:
  let lasts = _.uniq(sgms.map(sgm =>  { return sgm[sgm.length-1] }))
  // segments for dicts:
  let nonlasts = _.uniq(_.flatten(sgms.map(sgm =>  { return sgm.slice(0, -1) }) ))
  let pnonlasts = _.uniq(_.compact(nonlasts.map(nonlast => { return plain(nonlast) }) ))
  let max = _.max(pnonlasts.map(sec=>{ return sec.length}))
  d('max', max)
  // d('pnonlasts1___:', pnonlasts.toString())
  // if (compound) pnonlasts = _.filter(pnonlasts, sec=> { return sec.length < max-1})
  // d('pnonlasts2___:', pnonlasts.toString())

  let added = addedStems(pnonlasts)
  // added = []  // added необходимо добавлять, потому что reg impf, aor - в словаре только слабая форма
  // а вот non-reg из added можно убрать? то есть достройка только до splain, strong-plain ?

  let plainsegs = (added.length) ? _.uniq(pnonlasts.concat(added)) : pnonlasts
  plainsegs.push(cwf) // for terms

  d('cwf:', cwf)
  // d('sgms:', JSON.stringify(sgms))
  d('lasts:', lasts.toString())
  // d('pnonlasts:', pnonlasts.toString())
  // d('added:', added.toString())
  d('plainsegs:', plainsegs.toString())

  return Promise.all([
    getTerm(cwf),
    queryDBs(plainsegs),
    getFlex(lasts),
    getComp(plainsegs)
  ]).then(function (res) {
    let terms = _.flatten(res[0])
    let dicts = _.flatten(res[1])


    let dterms = _.filter(dicts, dict => { return dict.term && dict.term == cwf })
    terms = terms.concat(dterms)
    dterms = _.uniq(terms.map(dict => { return dict.term}))
    d('terms---->', dterms.toString())
    d('terms---->', terms.length)

    dicts = _.filter(dicts, dict => { return !dict.term })

    let comps = _.flatten(res[3])
    dicts = dicts.concat(comps)
    let flexes = _.flatten(res[2])

    dicts = _.filter(dicts, dict => { return !dict.indecl })
    let dplains = _.uniq(dicts.map(dict => { return dict.plain}))
    d('dictplains---->', dplains.toString())
    d('dicts.size---->', dicts.length)
    let complains = _.uniq(comps.map(dict => { return dict.plain}))
    d('complains---->', complains.toString())
    d('complains.size---->', complains.length)

    let prefs = _.filter(dicts, dict => { return dict.pref})
    let pprefs = _.uniq(prefs.map(dict => { return dict.plain}))
    d('prefs---->', pprefs.toString(), prefs.length)
    let sufs = _.filter(dicts, dict => { return dict.suf})
    let psufs = _.uniq(sufs.map(dict => { return dict.plain}))
    d('sufs---->', psufs.toString(), sufs.length)

    let kdicts = _.filter(dicts, dict => { return dict.plain == 'αρχ' })
    d('kdicts---->', kdicts.length) // , dicts = kdicts
    // d('flexes--->', flexes.length)
    // let kflexes = _.filter(flexes, fl => { return fl.tense == 'act.pres.ind' })
    // d('kflexes--->', kflexes.length) // , flexes = kflexes

    let segdicts = distributeDicts(plainsegs, dicts)
    // d('segdicts', segdicts['ιεραρχ'])

    // let chains = main(cwf, segdicts, sgms, pnonlasts, flexes, dicts, compound)
    // chains = _.compact(chains)
    let singles = _.filter(sgms, chain=> { return chain.length == 2 })
    d('singles', singles.length)

    // let chains = makeChains(sgms, segdicts, flexes)
    let chains = makeSingle(singles, segdicts, flexes)
    d('total single chains', chains.length)

    if (!chains.length) {
      let doubles = _.filter(sgms, chain=> { return chain.length == 3 })
      d('doubles', doubles.length)
      // добавить требование - длина dicts > 1 в compounds - longDicts? - если да, то отвалится ἀξίωσις - суффикс - σ
      // longDicts(segdicts)
      let doublechains = makeDouble(doubles, segdicts, flexes)
      chains.push(doublechains)
      chains = _.compact(_.flatten(chains))
      d('total double chains', chains.length)
      if (!chains.length) {
        let triples = _.filter(sgms, chain=> { return chain.length == 4 })
        d('triples', triples.length)
        let tripleschains = makeTriple(triples, segdicts, flexes)
        chains.push(tripleschains)
        chains = _.compact(_.flatten(chains))
        d('total triple chains', chains.length)
      }
    }

    // addDicts(chains, pnonlasts, segdicts)

    let bests = selectBest(chains, compound)
    d('bests =>', bests.length)
    if (bests.length) chains = bests

    d('result-chain--->', chains)
    chains.forEach(chain=>{
      // d('result-dicts--->', chain)
      let rdicts = chain.map(seg=> { return seg.dicts.map(dict=> { return dict.rdict }) })
      d('result-dicts--->', _.flatten(rdicts))
    })
    let result = {}
    if (chains.length) result.chains = chains
    if (terms.length) result.terms = terms
    if (chains.length && compound) result.compound = true
    return result
  })
}

function makeSingle (singles, segdicts, flexes) {
  let chains = []
  // log('______________________SINGLES', singles)
  singles.forEach(segs => {
    let lastseg = segs[1]
    let plastseg = plain(lastseg)
    let seg = segs[0]
    let pseg = plain(seg)
    let pdicts = segdicts[pseg]

    // если нужно будет использовать compounds для weight, то раскомментировать здесь, иначе попадет в adv
    pdicts = _.filter(pdicts, pdict=> { return !pdict.comp })
    // if (pseg == 'αξι') d('PDICTS: ===========================>>>', pdicts.length)
    // d('PDICTS: ===========================>>>', pseg, pdicts.length)

    let segflexes = _.filter(flexes, flex => { return flex.term === lastseg} )
    if (!segflexes.length) return

    let rdict = makeDict(segs, pdicts, segflexes)
    if (rdict) chains.push([rdict])
    // d('RDICT: ===========================>>>', pseg, rdict)

    // ADV
    let adv = makeAdv(segs, pdicts, segflexes)
    if (adv) adv.single = true
    if (adv) chains.push([adv])

  })
  return _.compact(chains)
}

function makeAdv(segs, pdicts, segflexes) {
  let advflexes = _.filter(segflexes, flex => { return flex.suf && flex.adv } ) // ἐλάχιστος - дает
  if (!advflexes.length) return
  let ndicts = _.filter(pdicts, pdict=> { return pdict.name || pdict.conn })
  if (!ndicts.length) return
  let seg = segs[0]
  let rdict = {seg: seg, dicts: ndicts, weight: 0 }

  rdict.adv = true
  rdict.term = true
  rdict.suffix = advflexes
  rdict.segs = JSON.stringify(segs)
  // rdict.fls_str = JSON.stringify(advflexes)
  // rdict.str = rdict.dicts.map(dict=> { return dict.rdict }).toString()
  // let adv = {seg: lastseg, dicts: advflexes, weight: 0, adv: true }
  // chains.push([rdict, adv])
  return rdict
}

function makeDict(segs, pdicts, segflexes) {
  // let dicts = _.filter(pdicts, pdict => { return pdict.name || (pdict.verb && pdict.plain.length > 1) || pdict.adv || pdict.part })
  let dicts = _.filter(pdicts, pdict => { return pdict.name || (pdict.verb && pdict.plain.length > 1) || pdict.part })
  if (!dicts.length) return
  // log('_____make-dicts', segs, dicts.length)
  let cdicts = makeDictFlex(dicts, segflexes)
  if (!cdicts.length) return
  // log('_____make-cdicts', segs, dicts.length)

  // I would like get all possible dicts - but olnly when? in combined? - иначе показывает чушь в обычной морфологии
  // let testdict = cdicts[0]
  // cdicts = _.filter(pdicts, pdict=> { return pdict.plain == testdict.plain })

  let strdicts = cdicts.map(cdict=> { return cdict.rdict }).toString()
  let commonseg = segs.join('')
  let rdict = {seg: commonseg, dicts: cdicts, weight: 0, str: strdicts }
  return rdict
}

function makeDictFlex (dicts, flexes) {
  let names = _.filter(dicts, dict => { return dict.name })
  let nflexes = _.filter(flexes, flex => { return flex.name })
  let verbs = _.filter(dicts, dict => { return dict.verb })
  let vflexes = _.filter(flexes, flex => { return flex.verb })

  // log('__________________________name-dicts', names.length, nflexes.length)
  let cdicts = parseName(names, nflexes)
  let vdicts = parseVerb(verbs, vflexes)
  cdicts.push(vdicts)
  cdicts = _.flatten(cdicts)
  // log('_____name-dicts', cdicts.length)
  return cdicts
}

function makeDouble (sgms, segdicts, flexes) {
  let chains = []
  // log('DOUBLE-SGMS', sgms)
  sgms.forEach(segs => {
    let headseg = plain(segs[0])
    let headdicts = segdicts[headseg]
    let stemseg = segs[1]
    let pstem = plain(stemseg)
    let stemdicts = segdicts[pstem]
    let lastseg = _.last(segs)
    let segflexes = _.filter(flexes, flex => { return flex.term === lastseg} )
    if (!segflexes.length) return

    // LONG
    headdicts = _.filter(headdicts, dict=> { return dict.plain.length > 1 })

    let prefs = _.filter(headdicts, pdict => { return pdict.pref })
    let smuts = _.filter(stemdicts, pdict => { return (pdict.verb && pdict.time == 'pres') || pdict.name || pdict.part })
    // let snames = _.filter(stemdicts, pdict => { return pdict.name })
    let hverbs = _.filter(headdicts, pdict => { return pdict.verb && pdict.time == 'pres' })
    let hnames = _.filter(headdicts, pdict => { return pdict.name })
    let hmuts = _.filter(headdicts, pdict => { return pdict.name || (pdict.verb && pdict.time == 'pres') })

    let chain, prefchain
    let dsgms = segs.slice(1)

    let connvow = vowels.includes(pstem) ? segs[1] : null
    let connector = {conn: true, seg: connvow, dicts: []}
    // let sadvnames = smuts
    let sadvnames = []
    if (connvow) sadvnames.push(connector)
    // log('___________________________________________double-CONVOW', connvow, segs, hnames.length, smuts.length, connvow)
    // ERR: ἐλάχιστος дает ἐλάχ-ι-στος
    // YYY ex: with connector - ἀγχόθι
    // либо если connector, записываnm chain раздельно - head, connector, adv-suffix
    let adv = makeAdv(dsgms, sadvnames, segflexes)
    // if (connvow) log('________________________DOUBLE-ADV', segs, connvow, sadvnames.length, segflexes.length, 'adv:', adv)
    if (adv) {
      adv.double = true
      let headadv = {seg: segs[0], dicts: hmuts, weight: 0}
      chains.push([headadv, adv])
    }

    if (prefs.length && smuts.length) {
      // if (pstem == 'αρχ') log('_____________________>>:', smuts)
      let prefseg = {seg: segs[0], dicts: prefs, weight: 0, pref: true}
      let prefrdict = makeDict(dsgms, smuts, segflexes)
      if (prefrdict) {
        prefchain = [prefseg, prefrdict]
        chains.push(prefchain)
      }
    }

    let sufdicts = _.filter(stemdicts, dict=> { return dict.suf })
    if (!sufdicts.length) return
    // log('_______________________________________________DOUBLE', segs, sufdicts.length, segflexes.length)
    let rdict = makeDict(dsgms, sufdicts, segflexes)
    // log('________________________DOUBLE-RDICT', segs, sufdicts.length, segflexes.length, 'rdict:', rdict.seg)

    if (!rdict) return
    rdict.str = rdict.dicts.map(dict=> { return dict.rdict }).toString()

    let vsufs = _.filter(sufdicts, pdict => { return pdict.verb })
    let vnsufs = _.filter(sufdicts, pdict => { return (pdict.verb && pdict.time == 'pres') && pdict.stem == 'name'  })
    let nnsufs = _.filter(sufdicts, pdict => { return pdict.name && pdict.stem == 'name'  })
    let nvsufs = _.filter(sufdicts, pdict => { return pdict.name && pdict.stem == 'verb'  })
    let vanysufs = _.filter(sufdicts, pdict => { return (pdict.verb && pdict.time == 'pres') && !pdict.stem  })

    // log('____________________________HERE', hnames.length, nnsufs.length)
    if (hverbs.length && vsufs.length) {
      let vseg = {seg: segs[0], dicts: hverbs, weight: 0}
      rdict.vsuf = true
      chain = [vseg, rdict]
    } else if (hnames.length && nnsufs.length) {
      let nseg = {seg: segs[0], dicts: hnames, weight: 0}
      rdict.nnsuf = true
      chain = [nseg, rdict]
    } else if (hverbs.length && nvsufs.length) {
      let nseg = {seg: segs[0], dicts: hverbs, weight: 0}
      rdict.nvsuf = true
      chain = [nseg, rdict]
    } else if (hnames.length && vnsufs.length) {
      let nseg = {seg: segs[0], dicts: hnames, weight: 0}
      rdict.vnsuf = true
      chain = [nseg, rdict]
    } else if (hmuts.length && vanysufs.length) {
      let nseg = {seg: segs[0], dicts: hmuts, weight: 0}
      rdict.vanysuf = true
      chain = [nseg, rdict]
    }
    if (chain) chains.push(chain)
  })
  return chains
}

function makeTriple (sgms, segdicts, flexes) {
  let chains = []
  sgms.forEach(segs => {
    let headseg = plain(segs[0])
    let headdicts = segdicts[headseg]
    let stemseg = segs[1]
    let pstem = plain(stemseg)
    let stemdicts = segdicts[pstem]
    let stemseg2 = segs[2]
    let pstem2 = plain(stemseg2)
    let stemdicts2 = segdicts[pstem2]
    let lastseg = _.last(segs)
    let segflexes = _.filter(flexes, flex => { return flex.term === lastseg} )
    // log('_______________________ TRIPLE segs', lastseg, segflexes.length)
    if (!segflexes.length) return

    let prefs = _.filter(headdicts, pdict => { return pdict.pref })
    let prefs2 = _.filter(stemdicts, pdict => { return pdict.pref })
    let smuts = _.filter(stemdicts, pdict => { return pdict.verb || pdict.name || pdict.part })
    let smuts2 = _.filter(stemdicts2, pdict => { return pdict.verb || pdict.name || pdict.part })

    let hverbs = _.filter(headdicts, pdict => { return pdict.verb })
    let hnames = _.filter(headdicts, pdict => { return pdict.name })
    let hmuts = _.filter(headdicts, pdict => { return pdict.name || pdict.verb })

    let chain, prefchain
    let dsgms = segs.slice(2)

    // нет примера, не сделано
    // let adv = makeAdv(dsgms, hnames, segflexes)
    // log('________________________TRIPLE-ADV', segs, hnames.length, segflexes.length, 'adv:', adv)
    // if (adv) chains.push([adv])

    if (prefs.length && prefs2.length && smuts2.length) {
      // log('_______________________________________________TRIPLE-PREF', segs, prefs.length, prefs2.length, smuts2.length )
      let prefseg = {seg: segs[0], dicts: prefs, weight: 0, pref: true}
      let prefseg2 = {seg: segs[1], dicts: prefs2, weight: 0, pref: true}
      let prefrdict = makeDict(dsgms, smuts2, segflexes)
      if (prefrdict) {
        prefchain = [prefseg, prefseg2, prefrdict]
        chains.push(prefchain)
      }
    }

    let connvow = vowels.includes(pstem) ? segs[1] : null
    // if (connvow) log('_______________________triple-connVOW', segs, connvow)
    // если убрать требование connvow, то нужно проверять, чтобы сумма сегментов была равна cwf
    if (!connvow) return

    let sufdicts = _.filter(stemdicts2, dict=> { return dict.suf })
    // if (connvow == 'ω') log('_______________________sufdicts', connvow, dsgms, headseg, headdicts.length, 'suf:', pstem2, sufdicts.length, 'fl:', segflexes.length)
    if (!sufdicts.length) return
    let rdict = makeDict(dsgms, sufdicts, segflexes)
    // log('_______________________________________________TRIPLE-RDICT', segs, connvow, sufdicts.length, segflexes.length, 'rdict:', rdict)
    if (!rdict) return

    rdict.str = rdict.dicts.map(dict=> { return dict.rdict }).toString()

    let vsufs = _.filter(sufdicts, pdict => { return pdict.verb })
    let vnsufs = _.filter(sufdicts, pdict => { return pdict.verb && pdict.stem == 'name'  })
    let nnsufs = _.filter(sufdicts, pdict => { return pdict.name && pdict.stem == 'name'  })
    let nvsufs = _.filter(sufdicts, pdict => { return pdict.name && pdict.stem == 'verb'  })
    let nanysufs = _.filter(sufdicts, pdict => { return pdict.name && !pdict.stem })

    let connector = {conn: true, seg: connvow, dicts: []}

    // log('____________________________TRIPLE', hnames.length, hverbs.length, nnsufs.length, nvsufs.length)
    let cwf = segs.join('')
    if (hverbs.length && vsufs.length && false) {
      if ([segs[0], rdict.seg].join('') != cwf) return
      let vseg = {seg: segs[0], dicts: hverbs, weight: 0}
      // rdict.vsuf = true
      rdict.suf = true
      chain = [vseg, rdict]
    } else if (connvow && hverbs.length && nvsufs.length) {
      if ([segs[0], connvow, rdict.seg].join('') != cwf) return
      let nseg = {seg: segs[0], dicts: hverbs, weight: 0}
      // rdict.nvsuf = true
      rdict.suf = true
      chain = [nseg, connector, rdict]
    } else if (connvow && hnames.length && nnsufs.length) {
      if ([segs[0], connvow, rdict.seg].join('') != cwf) return
      let nseg = {seg: segs[0], dicts: hnames, weight: 0}
      // rdict.nnsuf = true
      rdict.suf = true
      chain = [nseg, connector, rdict]
    } else if (hnames.length && vnsufs.length && false) {
      let nseg = {seg: segs[0], dicts: hnames, weight: 0}
      // rdict.vnsuf = true
      rdict.suf = true
      chain = [nseg, rdict]
    } else if (hmuts.length && nanysufs.length && false) {
      if ([segs[0], rdict.seg].join('') != cwf) return
      let nseg = {seg: segs[0], dicts: hmuts, weight: 0}
      // rdict.vnsuf = true
      rdict.suf = true
      chain = [nseg, rdict]
    }
    if (chain) chains.push(chain)
  })
  return chains
}

// предварительно распределяю dicts по всем plain-сегментам, включая добавочные dicts:
function distributeDicts (plainsegs, dicts) {
  let segdicts = {}
  plainsegs.forEach(pseg => {
    let sdicts = _.filter(dicts, dict => { return pseg === dict.plain })
    segdicts[pseg] = sdicts
  })
  return segdicts
}

function longDicts (segdicts) {
  for (let seg in segdicts) {
    segdicts[seg] = _.compact(_.filter(segdicts[seg], dict=> { return dict.plain.length > 1}))
  }
}

// ADDED
function addedStems(wforms) {
  let wplain, first, added = []
  wforms.forEach(wf => {
    first = _.first(wf)
    wplain = plain(wf)

    let weaks = strong2weak[first]
    if (!vowels.includes(first)) {
      let add = ['ε', wplain].join('') // aor.sub, opt, impf for consonants
      added.push(add)
    } else if (weaks) {
      weaks.forEach(weak => {
        let add = [weak, wf.slice(1)].join('') // impfs
        added.push(add)
      })
    }
  })
  return _.uniq(added)
}

function addDicts(chains, pnonlasts, segdicts) {
  chains.forEach(segs => {
    let penult = segs[segs.length-2]
    // if (!penult.dicts) d('NO SDICTS', segs)
    let seg = penult.seg
    seg = plain(seg)
    let first = _.first(seg)
    // if (penult.dicts.length) return // уже есть значение, добавлять не нужно - нет, так нельзя, ἦγον имеет ненужный ηγ и не пустит
    let sdicts = _.clone(penult.dicts)

    let weaks = strong2weak[first]
    if (!vowels.includes(first)) {
      let added = ['ε', seg].join('') // aor.sub, opt, impf from ind
      addDictCarefully(pnonlasts, segdicts, sdicts, added, 'added')
    } else if (first == 'ε') { // pas.aor.sub - βλέπω  - ἔβλεψα - ἐβλεφθῶ - dict plain βλε
      if (seg.length < 2) return
      let added = seg.slice(1)
      addDictCarefully(pnonlasts, segdicts, sdicts, added, 'sliced')
    } else if (first == 'η') { // mp.impf.ind - ἠβουλόμην, ἐβουλόμην
      if (seg.length < 2) return
      let added = seg.slice(1)
      addDictCarefully(pnonlasts, segdicts, sdicts, added, 'sliced')
    }
    if (weaks) {
      weaks.forEach(weak => {
        let stem = seg.slice(1)
        let added = [weak, stem].join('')
        addDictCarefully(pnonlasts, segdicts, sdicts, added, 'sliced')
      })
    }
    penult.dicts = _.compact(_.flatten(sdicts))
  })
}

function addDictCarefully(pnonlasts, segdicts, sdicts, added, mark) {
  let adicts = segdicts[added]
  adicts = _.filter(adicts, dict => { return dict.verb })
  if (!adicts || !adicts.length) return
  adicts.forEach(adict => {
    // есть случай, когда plain включен в исходные pnonlasts, но не в данной chain - ἐδείκνυν, plain δεικνυ в 3-chain
    // if (!pnonlasts.includes(adict.plain)) adict[mark] = true
    // но без проверки на pnonlasts.includes отвалится что-то важное потом?
    adict[mark] = true
  })
  sdicts.push(adicts)
}

function selectBest(chains, compound) {
  let shortests
  if (compound) shortests = chains
  else {
    let min = _.min(chains.map(chain => {  return chain.length } ) )
    shortests = _.filter(chains, chain => { return chain.length == min })
    // d('MIN', min, shortests.length)
  }
  return _.compact(shortests)
  // let max = _.max(shortests.map(chain=> { return _.sum(chain.map(seg=> { return seg.weight })) }) )
  // let haviests = _.filter(shortests, chain=>{ return _.sum(chain.map(seg=> { return seg.weight })) == max })
  // return haviests
}

function cleanStr(row) {
  let clean = row.trim()
  clean = clean.replace(/ᾰ/gi, 'α').replace(/ᾱ/gi, 'α').replace(/ῑ/gi, 'ι').replace(/ῐ/gi, 'ι').replace(/ῠ/gi, 'υ').replace(/ῡ/gi, 'υ')
  clean = clean.replace(/Ῐ/gi, 'Ι').replace(/Ῑ/gi, 'Ι')
  clean = clean.replace(/̆/gi, '')
  return clean
}

function corrStr(wf) {
  let clwf = cleanStr(wf)
  let cwf = comb(clwf)
  return oxia(cwf)
}

function enclitic(str) {
  let already = false
  let store = []
  str.split('').forEach(sym=> {
    if (sym == accents.oxia) {
      if (already) return
      already = true
    }
    store.push(sym)
  })
  return store.join('')
}

